<template lang="pug">
#home
  custom-header(
    :reservationNum="infoData.reservation.reservationNum",
    :goalNum="infoData.reservation.goalNum",
    :percent="infoData.reservation.percent"
  )
  custom-action
  custom-action-md
  product-intro
  comparison
  insured-amount
  ensure-description
  section.chart_rate_container
    .container.custom_container_md
      .row
        .col-lg-12
          h1.title 一起加入守護毛孩的行列
        .col-6.col-lg-3
          .pie_chart
            svg(viewbox="50 50 100 100")
              circle(cx=80, cy=80, r=80, style="fill:#f0b779")
              path#test1(
                ref="test1",
                style="fill:#ffd876; stroke:#fff; stroke-width:1;"
              )
            .rate_container.flex_h
              .rate.dog.flex_v
                h2 {{ infoData.petType.dog }}
                  span %
                .animal_container.flex_h
                  img(src="@/assets/img/home/fxemoji_dogside.svg")
                  p 汪汪
              .rate.cat.flex_v
                h2 {{ infoData.petType.cat }}
                  span %
                .animal_container.flex_h
                  img(src="@/assets/img/home/twemoji_cat.svg")
                  p 喵喵
        .col-6.col-lg-3
          .pie_chart
            svg(viewbox="50 50 100 100")
              circle(cx=80, cy=80, r=80, style="fill:#f0b779")
              path#test1(
                ref="test1",
                style="fill:#ffd876; stroke:#fff; stroke-width:1;"
              )
            .rate_container.flex_h
              .rate.man.flex_v
                h2 {{ infoData.petSex.male }}
                  span %
                .animal_container.flex_h
                  img(src="@/assets/img/home/man.svg")
                  p 男孩
              .rate.woman.flex_v
                h2 {{ infoData.petSex.female }}
                  span %
                .animal_container.flex_h
                  img(src="@/assets/img/home/woman.svg")
                  p 女孩
        .col-12.col-lg-6
          .pie_chart
      .row
        .col-lg-12.text-center
          .notify_next_action_btn
            h4 通知我下一檔活動
            img(src="@/assets/img/icon/next.svg")
  question
  contact
  introduction
  custom-footer
</template>

<script>
import CustomHeader from "@/components/CustomHeader";
import CustomFooter from "@/components/CustomFooter";
import CustomAction from "@/components/CustomAction";
import CustomActionMd from "@/components/CustomActionMd";
import ProductIntro from "@/components/ProductIntro";
import Comparison from "@/components/Comparison";
import InsuredAmount from "@/components/InsuredAmount";
import EnsureDescription from "@/components/EnsureDescription";
import Question from "@/components/Question";
import Contact from "@/components/Contact";
import Introduction from "@/components/Introduction";
import { fetchPageInfo } from "@/api/homepage_info";

var startAngle = 90;
var cx = 80;
var cy = 80;
var r = 80;
var deg1 = 360 * 0.38 + startAngle;

var x0 = cx + r * Math.cos((startAngle * Math.PI) / 180);
var y0 = cy - r * Math.sin((startAngle * Math.PI) / 180);

var x1 = cx + r * Math.cos((deg1 * Math.PI) / 180);
var y1 = cy - r * Math.sin((deg1 * Math.PI) / 180);

export default {
  name: "Home",
  components: {
    "custom-header": CustomHeader,
    "custom-footer": CustomFooter,
    "custom-action": CustomAction,
    "product-intro": ProductIntro,
    comparison: Comparison,
    "insured-amount": InsuredAmount,
    "ensure-description": EnsureDescription,
    question: Question,
    contact: Contact,
    introduction: Introduction,
    "custom-action-md": CustomActionMd,
  },
  data() {
    return {
      infoData: {
        petSex: {
          female: 0,
          male: 0,
        },
        petAge: {
          age_6: 0,
          age_5: 0,
          age_4: 0,
          age_3: 0,
          age_2: 0,
          age_1: 0,
        },
        petType: {
          cat: 0,
          dog: 0,
        },
        reservation: {
          reservationNum: 0,
          goalNum: 0,
          percent: 0,
        },
      },
    };
  },
  created() {
    this.getInfo();
  },
  methods: {
    getInfo() {
      fetchPageInfo().then((response) => {
        this.infoData = response.data.data;
      });
    },
  },
  mounted() {
    this.$refs.test1.setAttribute(
      "d",
      "M " +
        cx +
        "," +
        cy +
        " L " +
        x0 +
        "," +
        y0 +
        " A " +
        r +
        "," +
        r +
        " 0 0,0 " +
        x1 +
        "," +
        y1 +
        " Z"
    );
  },
};
</script>

