<script>
import petsImg from "@/assets/img/home/pets.jpg";
import pets2xImg from "@/assets/img/home/pets@2x.jpg";
import catToeBeanImg from "@/assets/img/home/cat-toe-bean.png";
import catToeBean2xImg from "@/assets/img/home/cat-toe-bean@2x.png";

export default {
  name: "ProductIntro",
  data() {
    return {
      imgs: {
        petsImg,
        pets2xImg,
        catToeBeanImg,
        catToeBean2xImg,
      },
      showBlock1: true,
      showBlock2: true,
      currentWidth: 0,
    };
  },
  mounted() {
    this.checkBlockStatusByWidth(this.$el.offsetWidth);
  },
  methods: {
    onResize({ width }) {
      this.checkBlockStatusByWidth(width);
    },
    checkBlockStatusByWidth(width) {
      if (this.currentWidth == 0) {
        if (width > 991) {
          this.showBlock1 = true;
          this.showBlock2 = true;
        } else {
          this.showBlock1 = true;
          this.showBlock2 = false;
        }
        this.currentWidth = width;
      } else {
        if (width > 991 && this.currentWidth < 991) {
          this.showBlock1 = true;
          this.showBlock2 = true;
          this.currentWidth = width;
        } else if (width < 991 && this.currentWidth > 991) {
          this.showBlock1 = true;
          this.showBlock2 = false;
          this.currentWidth = width;
        }
      }
    },
    block1Click() {
      this.showBlock1 = false;
      this.showBlock2 = true;
    },
    block2Click() {
      this.showBlock1 = true;
      this.showBlock2 = false;
    },
  },
};
</script>

