<template lang="pug">
header
  .container-md.custom_container_lg
    .row
      .header_media.col-xl-7.col-lg-6(v-resize="onResize")
        iframe(ref="ytplayer" type="text/html" src="https://www.youtube.com/embed/778oM7Vs5_o" frameborder="0" allowfullscreen :height="youtubeHeight")
      .header_event.col-xl-5.col-lg-6.text-center
        .header_event_title
          h2 寵圈圈 × 新安東京海上產險
          h2 毛保心安專案
        .header_event_description
          h5 最害怕毛小孩生病，醫療費兩、三萬，往往牙一咬就過去，怕的是動輒十幾二十萬的醫療費。而市面上的寵物險，卻沒辦法滿足這個需求？！
        .header_event_time
          p 預約投保時程
            span 2020/9/3 - 9/24
          .progress_bar_full
            .progress_bar(:style="changeProgressStyle")
        .header_event_info.flex_h
          .event_info_item.flex_v
            h4 目前人數
            p {{ reservationNum }}
          .event_info_item.flex_v
            h4 達標人數
            p {{ goalNum }}
          .event_info_item.flex_v
            h4 達成率
            p {{ percent }}%
</template>

<script>
export default {
  name: "CustomHeader",
  props: {
    reservationNum: Number,
    goalNum: Number,
    percent: Number,
  },
  data() {
    return {
      youtubeHeight: "auto"
    };
  },
  mounted() {
    this.youtubeHeight = this.$refs.ytplayer.clientWidth*9/16;
  },
  computed: {
    changeProgressStyle: function() {
      const progressStyle = {
        width: this.percent + "%",
        height: "12px",
        backgroundColor: "#feb100",
      };
      return progressStyle;
    },
  },
  methods: {
    onResize({ width }) {
      this.youtubeHeight = width*9/16;
    },
  },
};
</script>

