<template lang="pug">
section.comparison_container(v-resize="onResize")
  .container.custom_container_md
    .container
      .row
        .col
          h2.title 為何選擇
            span 新安東京海上寵物險？
      .product.row
        .tmni_product.col-lg-6(v-if="showTMNIProduct")
          .top_bar
          .product_title.notice_title_md.flex_h
            img(
              :srcset="`${imgs.fairytaleImg} 1x,${imgs.fairytale2xImg} 1.5x,${imgs.fairytale2xImg} 2x`",
              :src="imgs.fairytaleImg"
            )
            h5 新安東京海上
          h5.notice_btn_md(@click="TMNIProductClick") 其他市面上專案
          .product_content
            p 年齡滿 6 周即可投保，最高續保到 14 歲
            span （依晶片內記載之出生年月日起算）
            .divider
            p 年度保額內理賠次數無限制
            .divider
            p 門診、住院、手術無分項給付限制
            .divider
            p 傷害與疾病醫療、喪葬費用保障 免自負額
            .divider
        .other_product.col-lg-6(v-if="showOtherProduct")
          .top_bar
          .product_title.notice_btn_md.flex_h(@click="otherProductClick")
            img(
              :srcset="`${imgs.fairytaleImg} 1x,${imgs.fairytale2xImg} 1.5x,${imgs.fairytale2xImg} 2x`",
              :src="imgs.fairytaleImg"
            )
            h5 新安東京海上
          h5.notice_title_md 其他市面上專案
          .product_content
            p 多數 8 周才能投保，最高續保到 13 歲
            .divider
            p 通常有年度理賠次數的限制
            .divider
            p 醫療給付有門診、住院、手術分項限制
            .divider
            p 醫療給付有自負額或補償比例的限制
            .divider
            p 普遍保額較低，難彌補突發高額醫療費用
            .divider
            p 傷害與疾病醫療、喪葬費用保障 免自負額
            .divider
</template>

<script>
import fairytaleImg from "@/assets/img/home/icons-fairytale.png";
import fairytale2xImg from "@/assets/img/home/icons-fairytale@2x.png";

export default {
  name: "Comparison",
  data() {
    return {
      imgs: {
        fairytaleImg,
        fairytale2xImg,
      },
      showTMNIProduct: true,
      showOtherProduct: true,
      currentWidth: 0,
    };
  },
  mounted() {
    this.checkProductStatusByWidth(this.$el.offsetWidth);
  },
  methods: {
    onResize({ width }) {
      this.checkProductStatusByWidth(width);
    },
    checkProductStatusByWidth(width) {
      if (this.currentWidth == 0) {
        if (width > 991) {
          this.showTMNIProduct = true;
          this.showOtherProduct = true;
        } else {
          this.showTMNIProduct = true;
          this.showOtherProduct = false;
        }
        this.currentWidth = width;
      } else {
        if (width > 991 && this.currentWidth < 991) {
          this.showTMNIProduct = true;
          this.showOtherProduct = true;
          this.currentWidth = width;
        } else if (width < 991 && this.currentWidth > 991) {
          this.showTMNIProduct = true;
          this.showOtherProduct = false;
          this.currentWidth = width;
        }
      }
    },
    TMNIProductClick() {
      this.showTMNIProduct = false;
      this.showOtherProduct = true;
    },
    otherProductClick() {
      this.showTMNIProduct = true;
      this.showOtherProduct = false;
    },
  },
};
</script>

