<template lang="pug">
div(ref="action" v-resize="onResize")
  section.action_container(:class="{'is_top' : isTop}")
    .container.custom_container_lg
      .row.align-items-center
        .col-xl-7.col-lg-6
          h5.action_title 寵圈圈 × 新安東京海上產險 毛保心安專案
        .action.col-xl-5.col-lg-6.flex_h
          .main_btn(@click="reservationClick") 立即預約
          .main_btn.yellow(@click="trackClick") 追蹤活動
  div(:class="{'placeholder' : placeholder}")
</template>

<script>
export default {
  name: "Action",
  data() {
    return { fullWidth: 0, isTop: false, placeholder: false };
  },
  methods: {
    topAction() {
      var action = this.$refs.action;
      window.addEventListener("scroll", () => {
        this.$store.commit(
          "setActionTopMargin",
          action.getBoundingClientRect().top
        );
        if (action.getBoundingClientRect().top <= 95) {
          this.isTop = true;
          this.placeholder = true;
        } else {
          this.isTop = false;
          this.placeholder = false;
        }
      });
    },
    onResize({ width }) {
      this.checkScreenWidth(width);
    },
    checkScreenWidth(width) {
      if (width > 991) {
        if (this.$store.state.actionTopMargin <= 95) {
          if (!this.isTop) {
            this.isTop = true;
          }
        } else {
          if (this.isTop) {
            this.isTop = false;
          }
        }
      }
    },
    reservationClick() {
      this.$store.commit("setShowCustomDialogStatus", true);
      this.$store.commit("setTypeCustomDialog", 0);
    },
    trackClick() {
      this.$store.commit("setShowCustomDialogStatus", true);
      this.$store.commit("setTypeCustomDialog", 1);
    },
  },
  mounted() {
    var action = this.$refs.action;
    this.$store.commit(
      "setActionTopMargin",
      action.getBoundingClientRect().top
    );

    this.$nextTick(() => {
      this.topAction();
    });
  },
  activated() {
    this.$nextTick(() => {
      if (window.innerWidth >= 991) {
        var action = this.$refs.action;
        if (action.getBoundingClientRect().top <= 95) {
          this.isTop = true;
          this.placeholder = true;
        } else {
          this.isTop = false;
          this.placeholder = false;
        }
      }
      this.checkScreenWidth(this.$el.offsetWidth);
    });
  },
};
</script>

