<template lang="pug">
section.question_container
  .container.custom_container_md
    .row
      .col-lg-12
        h1.title 常見問題
      .col-lg-12
        a.question_btn(:class="{ question_btn_selected : questionFirstCheck}" @click="questionFirstClick") 投保規範及商品
        a.question_btn(:class="{ question_btn_selected : !questionFirstCheck}" @click="questionSecondClick") 預約投保流程
  standard-page-1(v-if="current_page==1 && questionFirstCheck")
  standard-page-2(v-if="current_page==2 && questionFirstCheck")
  standard-page-3(v-if="current_page==3 && questionFirstCheck")
  reservation-page-1(v-if="!questionFirstCheck")
  .container
    .row
      .col
      nav(aria-label='Page navigation example')
        ul.pagination(v-if="questionFirstCheck")
          li.page-item(:class="{ disabled : current_page==1}")
            a.page-link(@click="previousClick" aria-label='Previous')
              span(aria-hidden='true') <
          li.page-item(:class="{ current_page : current_page==1}")
            a.page-link(@click="current_page=1") 1
          li.page-item(:class="{ current_page : current_page==2}")
            a.page-link(@click="current_page=2") 2
          li.page-item(:class="{ current_page : current_page==3}")
            a.page-link(@click="current_page=3") 3
          li.page-item(:class="{ disabled : current_page==3}")
            a.page-link(aria-label='Next' @click="nextClick")
              span(aria-hidden='true') >

</template>

<script>
import StandardPage1 from "@/components/Question/standard/page1";
import StandardPage2 from "@/components/Question/standard/page2";
import StandardPage3 from "@/components/Question/standard/page3";
import ReservationPage1 from "@/components/Question/reservation/page1";

export default {
  name: "Question",
  components: {
    "standard-page-1": StandardPage1,
    "standard-page-2": StandardPage2,
    "standard-page-3": StandardPage3,
    "reservation-page-1": ReservationPage1,
  },
  data() {
    return {
      current_page: 1,
      questionFirstCheck: true,
    };
  },
  methods: {
    previousClick() {
      if (this.current_page > 1) {
        this.current_page = this.current_page - 1;
      }
    },
    nextClick() {
      if (this.current_page < 3) {
        this.current_page = this.current_page + 1;
      }
    },
    questionFirstClick() {
      this.questionFirstCheck = true;
    },
    questionSecondClick() {
      this.questionFirstCheck = false;
    },
  },
};
</script>

